#landing-main {
    margin: 0;
    height: 100vh;
    width: 100vw;
    background-color: #00151a;
    font-size: 200%;
    color: white;
}

.center-holder {
    position: absolute;
    z-index: 1000;
}

#landing-main .name {
    color: #34eb92;
}

.vertical-center {
    min-height: 100%;
    min-height: 100vh;

    display: flex;
    align-items: center;
}

.landing-btn {
    margin: 10px;
    padding: 10px 20px;
    border: 2px solid white;
    
    background: transparent;

    font-size: 65%;
    color: white;
}

.landing-btn:hover {
    border: 2px solid #67b5a4;
    background-color: #67b5a4;
    color: white;
    text-decoration: none;
}

#landing-text {
    position: relative !important;
}

@media screen and (max-width: 1200) {
    #root {
        font-size: 90%;
    }
}

@media screen and (max-width: 992px) {
    #root {
        font-size: 80%;
    }
}

@media screen and (max-width: 768px) {
    #root {
        font-size: 73%;
    }
}

@media screen and (max-width: 608px) {
    #root {
        font-size: 70%;
    }
}